'use strict';

(function() {
  let debounce = function (action, interval) {
    let lastTimeout;

    return function (arg) {
      if (lastTimeout) {
        window.clearTimeout(lastTimeout);
      }
      lastTimeout = window.setTimeout(action, interval, arg);
    };
  };

  window.util = {
    debounce: debounce
  }
})();
